body{
  font-family: $font-family__default;
  font-weight: 300;
  margin:0;
  padding:0;
  color: $color__default;
  //overflow-x: hidden;
  overflow: hidden;
}
 
.container{
  margin: 0 auto;
  max-width: 1800px;
  padding-right: 25px;
  padding-left: 25px;
  @media (max-width:$tablet-width-m) {
    padding-right: 10px;
    padding-left: 10px;
  }
  @media (max-width:$tablet-width-s) {
    padding-right: 5px;
    padding-left: 5px;
  }
}

img{
  max-width:100%;
  height:100%;
  object-fit:cover;
  object-position:50% 50%;
}

.data-scroll {
  overflow: hidden;
}
